const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    
    private speedup: number = 0;

    // onLoad () {}

    start () {
        var node = this.node;
        this.schedule(function(){
            this.speedup +=1;
        }, 3);
    }

    update (dt) {
        
        this.node.x -= (8+this.speedup);
        
        
        if(this.node.x < -1238){
            this.node.x = 1779;
        }
        //cc.audioEngine.playMusic(this.bgm,true);
        
    }
}
